body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card { 
  background: pink;
  border-radius: 2px;
  height: 200px;
  margin: 1rem;
  width: 200px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: center;
  /* position: relative; */
  display: inline-block;

}

.card > .image-container {
  overflow:hidden;
  text-align: center;
  display: center;
  width: 100%;
}

.card > .image-container {
  width: 100%;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.img-thumbnail:hover {
  background-color: rgb(255, 255,255);
  opacity: 0.5;
}
.wrapper {
  padding-top: 50px;
  background: lightcoral;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow:auto;
}
.Title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  text-decoration: underline;
  color: lightseagreen;
  margin-bottom: 50px;
}

.Left{
  display: inline;
  padding-left: 20px;
  margin-left: 20px;
  line-height: 20px;
  border-left: 5px solid lightblue;
  text-align: center;
}

.navbar {
  background: white;
  box-shadow: 0 2px 2px grey;
  z-index: 99;
  width: 100%;
  margin: 0 0 0px;
  overflow: auto;
}

li{
  color: lightcoral;
}

body {
  font-size:20px;
  font-family: Arial, Helvetica, sans-serif;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header{
   background-image: url("/https://www.sanrio.co.jp/common_v2/img/img_footer.gif");
  position: relative;
  width: 100%;
}

.jumbotronImage{
  background-image: url ("https://www.sanrio.co.jp/common_v2/img/img_footer.gif");
}

/* h1 is props */
h1{
  text-align: center;
  font-family: 'Brizel';
  font-size: 25px;
  font-weight: bold;
  color: lightcoral;
}

/* h2 is the clicky game title */
h2{
  text-align:center;
  font-size: 50px;
  padding-bottom: 20px;
  color: lightcoral;
}

/* h3 is rules */
h3{
  text-align:center;
  font-size: 25px;
  padding-bottom: 20px;
  color: lightcoral;
}

p{
  text-align: center;
  font-size: 20px;
  padding-bottom: 0px;
  color: lightcoral;
}
